.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.card-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 70%;
   /* 100% 뷰포트 높이 */ 
}

.small{
  height: 50px;
  transition: all 1s;
}

.card-effct {
  position: relative;
  text-align: center;
  border-radius: 10px;
  padding: 1rem;
  flex: 1;
  transition: 0.2s ease-in-out;
}

.card-effct:hover {
  transform: scale(0.9);
  border-radius: 50px;
  cursor: pointer;
}

.pokemon-parent{
  margin : 20px auto ;
  justify-content: center;
  align-items: center;
  display : flex ;
}

.pokemon-content{
  font-size: 20px;
  font-weight: bold;
}

.nextBtn{
  float: right;
  margin-left: 20px;

}
.backBtn{
  float: left;
  margin-right: 20px;
  
}

.id{
  color: grey;
}

.large{
  width: 100%;
  height: 11vh;
  transition: all 1s;
}

.start{
  opacity: 0;
}

.end{
  opacity: 1;
  transition: opacity 0.8s;
}


.logo-container {
  display: flex;
  justify-content: center;
  background-size: contain;
  align-items: center;
  height: 400px; /* 예시로 지정한 높이, 필요에 따라 조절하세요 */
  margin-bottom: 30px;
}

.logo-container img {
  object-fit: contain; /* 이미지를 요소에 맞춰서 표시 */
  max-width: 100%; /* 요소의 최대 너비에 맞추기 */
  max-height: 100%; /* 요소의 최대 높이에 맞추기 */
}

.card {
  width: 600px; /* 카드의 너비 설정 */
  border : 1px solid black ;
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
